import React from 'react';
import './CourseInformationDetail.scss';

class CourseInformationDetail extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			expand: false,
		};
	}

	onClickMore = () => {
		this.setState({
			expand: !this.state.expand,
		});
	}

	render() {
		return (
			<div className="course-information border">
				<div className="description">
					<h6 className="course-title">Thng tin kho hc</h6>
					<div dangerouslySetInnerHTML={{ __html: this.props.data }} />

						{/* {this.state.expand === true && (
							<p>- Ging vin: Ging vin kho hc giao tip c bn l nhng ging vin c hn 2 nm kinh nghim ging dy vi trnh  ting Anh tt (IELTS t t 7.0 / TOEIC t t 850). Cc Ging vin ca Edutalk l nhng thy c tr trung, y nng lng lun mang n cho lp hc mt bu khng kh vui ti v nng lng. Cc Ging vin l cc Thy C yu ngh lun lun thu hiu v gip  hc vin trong qu trnh dy hc.</p>
						)}
					</div>

					<button className="more-btn" onClick={this.onClickMore}>{this.state.expand === false ? 'Xem thm' : 'Thu gn'}</button> */}
				</div>
			</div>
		)
	}
}

export default CourseInformationDetail;