import React from 'react';
import { connect } from 'react-redux';
import { withRouter, Redirect } from 'react-router-dom';
import { AiFillFacebook, AiOutlineGoogle } from 'react-icons/ai';
import { isEmpty, isInValidEmail } from 'ultis/commom';
import { login } from './redux';
import './Login.scss';

class Login extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isSignUp: false,
            username: '',
            usernameError: '',
            password: '',
            passwordError: '',
            redirect: false,
        };

        if (this.props.hideComp) {
            this.props.hideComp();
        }
    }

    componentDidUpdate() {
        if (this.props.authentication.status === true) {
            this.setState({
                redirect: true,
            });
            this.props.showComp();
        }
    }

    changeMode = () => {
        this.setState({
            isSignUp: !this.state.isSignUp,
        });
    }

    submit = (event) => {
        this.validateInput();
        event.preventDefault();
        const data = new FormData(event.target);
        const params = {
            data,
        };

        if (this.state.isSignUp === false) {
            this.login(params);
        }
    }

    login = (params) => {
        this.props.login(params);
    }

    onChangeUsername = (event) => {
        this.setState({
            username: event.target.value,
            usernameError: null,
        });
    }

    onChangePassword = (event) => {
        this.setState({
            password: event.target.value,
            passwordError: null,
        });
    }

    validateInput = () => {
        let usernameError = null;
        let passwordError = null;

        if (isEmpty(this.state.password) === true) {
            passwordError = '*Vui lng nhp mt khu';
        } else {
            passwordError = null;
        }

        if (isEmpty(this.state.username) === true) {
            usernameError = '*Vui lng nhp email';
        } else if (isInValidEmail(this.state.email) === true) {
            usernameError = '*Bn in sai email. Vui lng nhp li email';
        } else {
            usernameError = null;
        }

        this.setState({
            usernameError,
            passwordError,
        });
    }

    render() {
        if (this.state.redirect === true) {
            if (this.props.location.state.previousPath != null) {
                return <Redirect to={this.props.location.state.previousPath} />
            } else {
                return <Redirect to="/" />
            }
        }

        return (
            <div className="login-container">
                <div className="login-row header">
                    <h3 className="title">Nn tng kt ni ging vin uy tn</h3>
                    <span className="text btn" onClick={this.changeMode}>{this.state.isSignUp !== true ? 'ng k ti khon mi' : 'ng nhp'}</span>
                </div>
                <div className="login-row">
                    <span className="note-text">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</span>
                </div>
                <div className="login-row content">
                    <form method="POST" onSubmit={this.submit} className="content-left">
                        <span className="label">ng nhp nhanh vi</span>
                        <div className="login-btn-list">
                            <button className="facebook-btn btn"><AiFillFacebook className="icon" /><span>Facebook</span></button>
                            <button className="google-btn btn"><AiOutlineGoogle className="icon" /><span>Google</span></button>
                        </div>
                        <span>{this.state.isSignUp !== true ? 'ng nhp bng' : 'ng k ngay'}</span>
                        <div className={`login-input`}>
                            {this.state.isSignUp === true && <input className="name" type="text" placeholder="H tn" />}
                            <input type="text" name="email" onChange={this.onChangeUsername} placeholder="Email hoc s in thoi" />
                            <input type="password" name="password" autoComplete="true" onChange={this.onChangePassword} className="password" placeholder="Mt khu" />
                        </div>
                        {this.state.isSignUp === true && <span className="signup-note">Bng cch nhp vo ng k, bn ng  vi <b>iu khon</b>, <b> Quy ch hot ng</b> v <b>Chnh sch bo mt</b> ca Edutalk.</span>}
                        <div className="login-footer">
                            <button className="submit-btn btn">{this.state.isSignUp !== true ? 'ng nhp' : 'ng k'}</button>
                            {this.state.isSignUp !== true ? <a href="/">Qun mt khu ?</a> : <span className="has-account btn" onClick={this.changeMode}> c ti khon</span>}
                        </div>
                    </form>
                    <div className="content-right">
                        <img src={`${process.env.PUBLIC_URL}/assets/images/logo/login-logo.png`} alt="" />
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    authentication: state.Authentication,
});

const mapDispatchToProps = dispatch => ({
    login: payload => dispatch(login(payload))
});

export default (
    connect(
        mapStateToProps,
        mapDispatchToProps,
    )(withRouter(Login))
);