import React from 'react';
import './Footer.scss';
import { Link } from 'react-router-dom';

const introList = [
	{
		name: 'V Edutalk',
		link: '/about',
	},
	{
		name: 'iu khon',
		link: '/',
	},
	{
		name: 'Quy nh vit nh gi',
		link: '/',
	},
	{
		name: 'Bo mt',
		link: '/',
	},
	{
		name: 'Lin h',
		link: '/contact',
	},
];

const discoverList = [
	{
		name: 'nh gi',
		link: '/',
	},
	{
		name: 'Tin tc s kin',
		link: '/',
	},
	{
		name: 'Thng tin kho hc',
		link: '/courses',
	},
	{
		name: 'Thng tin ging vin',
		link: '/teachers',
	},
];

const stakeholderList = [
	{
		name: 'Dnh cho trung tm',
		link: '/',
	},
	{
		name: 'Dnh cho ging vin',
		link: '/',
	},
];

class Footer extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {
		return (
			<footer>
				<div className="footer">
					<div className="container ">
						<div className="row footer-content">
							<div className="col-2">
								<span>Gii thiu</span>
								<ul className="footer-list">
									{introList.map((item, index) => {
										return (
											<li key={index} className="footer-item"><Link className="footer-link" to={item.link}>{item.name}</Link></li>
										)
									})}
								</ul>
							</div>
							<div className="col-2">
								<span>Khm ph</span>
								<ul className="footer-list">
									{discoverList.map((item, index) => {
										return (
											<li key={index} className="footer-item"><Link className="footer-link" to={item.link}>{item.name}</Link></li>
										)
									})}
								</ul>
							</div>
							<div className="col-2">
								<span>Hp tc</span>
								<ul className="footer-list">
									{stakeholderList.map((item, index) => {
										return (
											<li key={index} className="footer-item"><Link className="footer-link" to={item.link}>{item.name}</Link></li>
										)
									})}
								</ul>
							</div>
							<div className="col-4 footer-facebook">
								<div className="fb-page"
									data-href="https://www.facebook.com/Edutalk.edu.vn/"
									data-width="340"
									data-hide-cover="false"
									data-show-facepile="true"></div>
							</div>
						</div>
					</div>
					<div className="footer-end">
						<div className="container footer-end-container">
							<div className="row footer-end-content">
								<div className="col-6 footer-end-item">
									<img src={`${process.env.PUBLIC_URL}/assets//images/logo/Logo-edutalk.png`} height="42px" width="23px"
										className="logo-home" alt="" />
								</div>
								<div className="col-6 footer-end-item">
									<span className="copy-right"> 2019, Bn quyn thuc Edutalk</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</footer>
		)
	}
}

export default Footer;