import React from 'react';
import './AccountInfo.scss';

class AccountInfo extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};
    }

    render() {
        return (
            <div className="account-info">
                <div className="header">
                    <div className="header-label">Thng tin ti khon</div>
                    <button className="student-rule">iu khon vi hc vin</button>
                </div>

                <div className="item">
                    <div className="col-6 account-input input-padding">
                        <label className="label" htmlFor="">H tn:</label>
                        <input className="input" defaultValue="Trn Vn A" type="text" />
                    </div>
                    <div className="col-6 account-input">
                        <label className="label" htmlFor="">S in thoi:</label>
                        <input className="input" defaultValue="0326 483 884" type="text" />
                    </div>
                </div>

                <div className="item">
                    <div className="account-input input-padding no-margin">
                        <label className="label" htmlFor="">a ch:</label>
                        <input className="input" defaultValue="151 Nguyn c Cnh, Hong Mai, H Ni" type="text" />
                    </div>
                </div>

                <div className="item">
                    <div className="account-input">
                        <label className="label" htmlFor="">Tnh / TP:</label>
                        <input className="input" defaultValue="H Ni" type="text" />
                    </div>
                    <div className="account-input" style={{
                        marginRight: '30px',
                        marginLeft: '30px'
                    }}>
                        <label className="label" htmlFor="">Qun/ Huyn:</label>
                        <input className="input" defaultValue="Hong Mai" type="text" />
                    </div>
                    <div className="account-input">
                        <label className="label" htmlFor="">Phng/ Th x:</label>
                        <input className="input" defaultValue="Tn Mai" type="text" />
                    </div>
                </div>
            </div>
        )
    }
}

export default AccountInfo;